@font-face {
    font-family: 'change-webfont';
    src: url('/assets/fonts/Change-Regular.woff2') format('woff2'),
        url('/assets/fonts/Change-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'change-webfont';
    src: url('/assets/fonts/Change-Bold.woff2') format('woff2'),
        url('/assets/fonts/Change-Bold.woff') format('woff');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

:root {
    --color-3: #48453E;
}

html {
    background: url('/assets/images/torn-paper-narrow-bg.jpeg') no-repeat;
    background-size: 40em;
    background-position: top 30em center;
    color: #48453E;
    font-family: 'change-webfont', Georgia, 'Times New Roman', Times, 'SongTi TC', serif;
    font-variant-numeric: lining-nums;
}

header {
    margin-bottom: 2.5em;
}

header picture img {
    margin: 0 auto;
}

@media (min-width: 600px) {
    html {
        background: url('/assets/images/torn-paper-bg.jpeg') no-repeat;
        background-size: 62.5em;
        background-position: top 5em center;
    }

    header {
        margin-bottom: 0;
    }

    main {
        rotate: -2deg;
        padding-left: 4em;
        padding-right: 0;
    }
}